import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import CssBaseline from '@material-ui/core/CssBaseline'
// import styled from '@material-ui/styles/styled';
import { withStyles } from '@material-ui/core/styles'

// import muiTheme from '../utils/theme';

import Navbar from './Navbar'
// import './all.scss';

// const theme = createMuiTheme(muiTheme);

const styles = theme => ({
  heroUnit: {
    // backgroundColor: theme.palette.background.paper,
    backgroundColor: theme.palette.grey[200],
  },
  containerFluid: {
    width: '100%',
    paddingRight: '15px',
    paddingLeft: '15px',
    marginRight: 'auto',
    marginLeft: 'auto',
  },
  stickToBottom: {
    width: '100%',
    position: 'fixed',
    bottom: 0,
  },
  appBarSpacer: theme.mixins.toolbar,
  // content: {
  //   flexGrow: 1,
  //   backgroundColor: theme.palette.grey[200],
  //   // padding: theme.spacing.unit,
  //   // height: '100vh',
  //   // overflow: 'auto',
  // },
  contentPadding: {
    flexGrow: 1,
    // backgroundColor: theme.palette.grey[200],
    padding: theme.spacing.unit,
  },
  content: {
    maxWidth: 1200,
    padding: theme.spacing.unit * 2,
    margin: 'auto',
  },
})

// const Content = styled('div')({
//   maxWidth: 1000,
//   padding: theme.spacing.unit * 4,
//   margin: 'auto',
// });
const appUrl = 'https://www.kalviapp.com'
// const image = "https://avatars3.githubusercontent.com/u/40117789";

const baseSchema = [
  {
    '@context': 'http://schema.org',
    '@type': 'WebSite',
    url: appUrl,
    name: 'kalviapp',
  },
]

class TemplateWrapper extends React.PureComponent {
  // state = {
  //   value: 0,
  // };

  // handleChange = (event, value) => {
  //   console.log('value111');
  //   console.log(value);
  //   this.setState({ value });
  // };
  // getRouteValue(type) {
  //   const pathValues = {
  //     '/': 0,
  //     '/news': 1,
  //     '/contact': 2,
  //     '/about': 3,
  //     default: 0,
  //   };
  //   return pathValues[type] || pathValues.default;
  // }

  render() {
    // const { classes } = this.props;
    const {
      classes,
      children,
      isShowBack,
      title,
      description,
      pathname,
    } = this.props
    // console.log(pathname)
    const url = `${appUrl}${pathname}`
    const displayTitle = title
      ? `KalviApp -  - ${title}`
      : 'KalviApp - '
    const displayDescription = description
      ? `KalviApp -  - ${description}`
      : 'KalviApp - '
    // const value = this.getRouteValue(pathname);
    // console.log('pathname pathname');
    // console.log(pathname);

    return (
      <React.Fragment>
        {/* <MuiThemeProvider theme={theme}> */}
        <CssBaseline />
        <Helmet>
          <title>{displayTitle}</title>
          <meta name="description" content={displayDescription} />
          <link rel="canonical" href={url} />
          <script type="application/ld+json">
            {JSON.stringify(baseSchema)}
          </script>
          {/* OpenGraph tags */}
          <meta property="og:url" content={url} />
          <meta property="og:title" content={displayTitle} />
          <meta property="og:description" content={displayDescription} />
        </Helmet>
        {/* <Helmet title="KalviApp -  -     - Tamilnadu Samacheer Textbooks 2017, 2018 -  , NEET   /,   Online Test" /> */}
        <Navbar isShowBack={isShowBack} />
        <main>
          <div className={classes.appBarSpacer} />
          <div className={classes.content}>
            {children}
            {/* <div className={classes.contentPadding}> */}
            {/* <div className={pathname === '/' ? classes.content : classes.contentPadding }> */}
            {/* <div className={classes.containerFluid}>{children}</div> */}
            {/* {children}
          </div> */}
          </div>
        </main>
        <div className={classes.appBarSpacer} />
        {/* <BottomNavigation
          value={value}
          // onChange={this.handleChange}
          showLabels
          className={classes.stickToBottom}
        >
          <BottomNavigationAction
            component={Link}
            to="/"
            label=""
            icon={<HomeIcon />}
          />
          <BottomNavigationAction
            component={Link}
            to="/blog"
            label=""
            icon={<SubjectIcon />}
          />
          <BottomNavigationAction
            component={Link}
            to="/contact"
            label=""
            icon={<FeedbackIcon />}
          />
          <BottomNavigationAction
            component={Link}
            to="/about"
            label=""
            icon={<PersonIcon />}
          />
        </BottomNavigation> */}
        {/* </MuiThemeProvider> */}
      </React.Fragment>
    )
  }
}

TemplateWrapper.propTypes = {
  children: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  isShowBack: PropTypes.bool,
  pathname: PropTypes.string,
}

// export default TemplateWrapper;
// export default withRoot(withStyles(styles)(TemplateWrapper));
export default withStyles(styles)(TemplateWrapper)
