import React from 'react'
import { Link } from 'gatsby'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Paper from '@material-ui/core/Paper'
import { graphql } from 'gatsby'
import Layout from '../components/Layout'
import withRoot from '../withRoot'

// export default () => <div>Hello world!..</div>

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
  },
  card: {
    width: 300,
    margin: theme.spacing.unit,
    [theme.breakpoints.down('sm')]: {
      minWidth: '100%',
    },
  },
  content: {
    ...theme.mixins.gutters(),
    marginTop: theme.spacing.unit * 2,
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  },
})

class AutonomousCollegePageMainPage extends React.Component {
  render() {
    const { classes, data, location } = this.props
    // console.log(data);
    const autoColl = data.allGetCollegesJson.edges.filter(item => item.node.gender === 2);
    // console.log(autoColl);
    return (
      <Layout pathname={location.pathname}>
<Typography variant="h4" gutterBottom>
Tamilnadu Engineering Womens Colleges
      </Typography>
{autoColl.map(item => {
              return (
                <Paper
                  className={classes.content}
                  elevation={1}
                  key={item.node.id}
                >
                  <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="flex-start"
                  >
                    <Typography
                      variant="h6"
                      gutterBottom
                      color="primary"
                      component={Link}
                      to={`/${item.node.slug}`}
                    >
                      {item.node.name}
                    </Typography>

                    <Typography
                      variant="subtitle1"
                      gutterBottom
                      color="secondary"
                    >
                      {item.node.collcode}
                    </Typography>
                  </Grid>
                  <Typography variant="body1" gutterBottom>
                    {item.node.add1}
                  </Typography>
                  <Typography variant="body2" gutterBottom>
                    Year of Starts: {item.node.yos}
                  </Typography>
                </Paper>
              )
            })}      
      </Layout>
    )
  }
}
export const query = graphql`
  query womendCollegeQuery {
    allGetCollegesJson {
    edges {
      node {
        id
        name
        collcode
        locality
        slug
        gender
        lat
        lng
        place
        add1
        add2
        locality
        phone_no1
        www
        autonomous
        phone_1
        yos
        phone_2
        email      
      }
    }
  }
  }
`

export default withRoot(withStyles(styles)(AutonomousCollegePageMainPage));
