import React from 'react'
import { Link } from 'gatsby'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Button from '@material-ui/core/Button'
import Layout from '../components/Layout'
import withRoot from '../withRoot'

// export default () => <div>Hello world!..</div>

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
  },
  card: {
    width: 300,
    margin: theme.spacing.unit,
    [theme.breakpoints.down('sm')]: {
      minWidth: '100%',
    },
  },
})

const IndexPage = ({ classes, location }) => (
  <Layout pathname={location.pathname}>
    <Grid container direction="row" justify="center" alignItems="center">
      <Card className={classes.card}>
        <CardContent>
          <Typography component="p">
            Search by College Names
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            component={Link}
            to={'/tamilnadu-engineering-colleges'}
            color="primary"
          >
            Tamilnadu Engineering Colleges
          </Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography component="p">
            Search by Autonomous Colleges
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            component={Link}
            to={'/tamilnadu-engineering-autonomous-colleges'}
            color="primary"
          >
            Autonomous Colleges
          </Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography component="p">
            Search by Branch names
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            component={Link}
            to={'/branches'}
            color="primary"
          >
            Branche Names
          </Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography component="p">
          Search by Womens Colleges
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            component={Link}
            to={'/tamilnadu-engineering-womens-colleges'}
            color="primary"
          >
            womens Colleges
          </Button>
        </CardActions>
      </Card>
      <Card className={classes.card}>
        <CardContent>
          <Typography component="p">
            Search by Counselling Codes
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            component={Link}
            to={'/tnea-counselling-codes'}
            color="primary"
          >
            Counselling Codes
          </Button>
        </CardActions>
      </Card>
    </Grid>
    {/* <Paper className={classes.root} elevation={1}>
      <Typography variant="body1" gutterBottom>
           
      </Typography>
    </Paper> */}
  </Layout>
)

export default withRoot(withStyles(styles)(IndexPage))
