import React from 'react'
import PropTypes from 'prop-types'
import faviconStudy from './img/study.png'

export default class HTML extends React.PureComponent {
  render() {
    return (
      <html>
        <head>
          <meta charSet="utf-8" />
          <meta name="theme-color" content="#e91e63" />
          <meta
            name="google-site-verification"
            content="bmLAmqchV4Ss7hKlLUZZYb1sR53qFT74KuUSmIcf-qo"
          />
          <meta
            name="viewport"
            content="width=device-width, initial-scale=1, shrink-to-fit=no"
          />
          <link rel="icon" type="image/png" href={faviconStudy} />
          {this.props.headComponents}
          <script async src="//pagead2.googlesyndication.com/pagead/js/adsbygoogle.js" />
          <script>
                  {`
                  (adsbygoogle = window.adsbygoogle || []).push({
                    google_ad_client: "ca-pub-2445138966914411",
                    enable_page_level_ads: true
                  });
                  `}
             </script>                    
          {/* <script type="application/ld+json">{`{"@context":"http://schema.org","@type":"Product","name":"    Tamilnadu Samacheer Textbooks 2017, 2018 ","image":"https://lh3.googleusercontent.com/qfXuLFNdqZfvEiHH1dy1F-cHj0cdL-72GgNmBrYY2cAsekAefW8eCqAGVcsFY_hbbX7A=w300-rw","description":"Books can be download class, subject and term wise. Each page has the option to take notes while study.</P>\n            <P class=\"lead text-muted\">This will be helpful for students, teachers and who preparing for civil service examinations, TNPSC Group exams, and competitive examinations.","url":"https://play.google.com/store/apps/details?id=org.saravanakumar.mytextbooks","brand":{"@type":"Brand","name":"Tamilnadu Samacheer Textbooks","logo":"https://lh3.googleusercontent.com/qfXuLFNdqZfvEiHH1dy1F-cHj0cdL-72GgNmBrYY2cAsekAefW8eCqAGVcsFY_hbbX7A=w300-rw"},"offers":{"@type":"Offer","price":"free"}}`}</script> */}
        </head>
        <body {...this.props.bodyAttributes}>
          {this.props.preBodyComponents}
          <div
            key={`body`}
            id="___gatsby"
            dangerouslySetInnerHTML={{ __html: this.props.body }}
          />
          {this.props.postBodyComponents}
        </body>
      </html>
    )
  }
}

HTML.propTypes = {
  htmlAttributes: PropTypes.object,
  headComponents: PropTypes.array,
  bodyAttributes: PropTypes.object,
  preBodyComponents: PropTypes.array,
  body: PropTypes.string,
  postBodyComponents: PropTypes.array,
}
