// import * as PropTypes from 'prop-types';
import React from 'react'
import { Link } from 'gatsby'
import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import Button from '@material-ui/core/Button'
import { withStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import Typography from '@material-ui/core/Typography'
import Divider from '@material-ui/core/Divider'
import ListItemText from '@material-ui/core/ListItemText'
// import PostDetail from "../components/post-detail"
// import Layout from "../layouts"
import withRoot from '../withRoot'
import Layout from '../components/Layout'
// import AdhikarmList from '../components/AdhikarmList';
// import ThirukkuralAdhikaramDisplay from '../components/ThirukkuralAdhikaramDisplay';

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  },
  content: {
    ...theme.mixins.gutters(),
    marginTop: theme.spacing.unit * 2,
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  },
})

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />
}

class CollegePageStartsTemplate extends React.Component {
  // static propTypes = {
  //   data: PropTypes.shape({
  //     postsJson: PropTypes.object.isRequired,
  //   }),
  // };
  render() {
    const { location, pageContext, classes } = this.props
    const { colleges, pageStartsArray } = pageContext
    // console.log(this.props);
    // console.log(colleges)
    // console.log(allThirukkuralExplain);
    // console.log(this.props);
    // const { thirukkuralJson: posts } = data;
    // console.log(data);
    // We don't want to show the modal if a user navigates
    // directly to a post so if this code is running on Gatsby's
    // initial render then we don't show the modal, otherwise we
    // do.
    // if (
    //   typeof window !== `undefined` &&
    //   window.___GATSBYGRAM_INITIAL_RENDER_COMPLETE
    // ) {
    //   isModal = true
    // }
    // const adhikarmNames = uniqBy(allThirukkural, 'node.adhikarm_no');
    // // console.log(flattenDeep(thirukkuralDetail));
    // // console.log(adhikarmNames);
    // const currentAdhikaram = items[0].node;

    return (
      <Layout pathname={location.pathname}>
        <Grid container spacing={8}>
          <Grid item xs={12} md={8}>
            {/* <Grid
              container
              direction="row"
              justify="space-between"
              alignItems="center"
            >
              <Button
                component={Link}
                size="small"
                className={classes.button}
                color="secondary"
                to={'/thirukkural'}
              >
                <ArrowBackIcon className={classes.rightIcon} />
                {'  '}
              </Button>
            </Grid> */}

            {colleges.map(item => {
              return (
                <Paper
                  className={classes.content}
                  elevation={1}
                  key={item.node.id}
                >
                  <Grid
                    container
                    direction="row"
                    justify="space-between"
                    alignItems="flex-start"
                  >
                    <Typography
                      variant="h6"
                      gutterBottom
                      color="primary"
                      component={Link}
                      to={`/${item.node.slug}`}
                    >
                      {item.node.name}
                    </Typography>

                    <Typography
                      variant="subtitle1"
                      gutterBottom
                      color="secondary"
                    >
                      {item.node.collcode}
                    </Typography>
                  </Grid>
                  <Typography variant="body1" gutterBottom>
                    {item.node.add1}
                  </Typography>
                  <Typography variant="body2" gutterBottom>
                    Year of Starts: {item.node.yos}
                  </Typography>
                </Paper>
              )
            })}
            {/* <Paper className={classes.root} elevation={1}>
              <List>
                {colleges.map(item => {
                  return (
                    <React.Fragment key={item.node.id}>
                      <ListItem>
                        <Grid container direction="column">
                          <ListItemLink href={`/${item.node.slug}`}>
                            <Typography variant="body1" gutterBottom>
                              {item.node.name}
                            </Typography>
                          </ListItemLink>
                        </Grid>
                      </ListItem>
                      <Divider />
                    </React.Fragment>
                  )
                })}
              </List>
            </Paper> */}
          </Grid>
          <Grid item xs={12} md={4}>
            <Paper elevation={1}>
              <List>
                {pageStartsArray.map(startsItem => (
                  <React.Fragment key={startsItem}>
                    <ListItemLink
                      href={`/tamilnadu-engineering-colleges-starts-with-${startsItem}`}
                    >
                      <ListItemText
                        primary={`Colleges starts with - ${startsItem.toUpperCase()}`}
                      />
                    </ListItemLink>
                    <Divider />
                  </React.Fragment>
                ))}
              </List>
            </Paper>
          </Grid>
        </Grid>
      </Layout>
    )
  }
}

// export default ThirukkuralChapterTemplate;
// export default ThirukkuralSingleTemplate;
export default withRoot(withStyles(styles)(CollegePageStartsTemplate))

