import React from 'react'
import { Link } from 'gatsby'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import Button from '@material-ui/core/Button'
import Layout from '../components/Layout'
import withRoot from '../withRoot'

// export default () => <div>Hello world!..</div>

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
  },
  card: {
    width: 300,
    margin: theme.spacing.unit,
    [theme.breakpoints.down('sm')]: {
      minWidth: '100%',
    },
  },
  margin: {
    margin: theme.spacing.unit,
  },
})

class CollegePageMainPage extends React.Component {
  // static propTypes = {
  //   data: PropTypes.shape({
  //     postsJson: PropTypes.object.isRequired,
  //   }),
  // };
  state = {
    pageStartsArray: [],
  }

  componentDidMount() {
    const first = 'a'
    const last = 'z'
    let pageStartsArray = []
    for (let i = first.charCodeAt(0); i <= last.charCodeAt(0); i++) {
      const _char = String.fromCharCode(i)
      pageStartsArray.push(_char)
    }
    this.setState({
      pageStartsArray: pageStartsArray,
    })
  }
  render() {
    const { classes, location } = this.props
    return (
      <Layout pathname={location.pathname}>
        <Grid container direction="row" justify="center" alignItems="center">
          {this.state.pageStartsArray.map(item => {
            return (
              <React.Fragment key={item}>
                <Button
                  variant="outlined"
                  size="small"
                  color="primary"
                  className={classes.margin}
                  component={Link}
                  to={`/tamilnadu-engineering-colleges-starts-with-${item}`}
                >
                  {item}
                </Button>
              </React.Fragment>
            )
          })}

          {/* <GridList cellHeight={180} className={classes.gridList}>
        <GridListTile key="Subheader" cols={2} style={{ height: 'auto' }}>
          <ListSubheader component="div">December</ListSubheader>
        </GridListTile>
                {this.state.pageStartsArray.map(item => {
                  return (
                    <React.Fragment key={item}>
                      <ListItem>
                        <Grid container direction="row">
                            <Button size="small" className={classes.margin}>
                            {item}
        </Button>
                        </Grid>
                      </ListItem>
                    </React.Fragment>
                  )
                })}
              </GridList> */}
        </Grid>
      </Layout>
    )
  }
}

export default withRoot(withStyles(styles)(CollegePageMainPage))
