import React from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import withRoot from '../../withRoot';
import Layout from '../../components/Layout';

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
  },
});

const ContactPage = ({ classes, location }) => (
  <Layout pathname={location.pathname}>
    <Paper className={classes.root} elevation={1}>
      <Typography variant="body1" gutterBottom>
        Please share your Feedback or If any Wrong information.
      </Typography>
      <iframe
        src="https://docs.google.com/forms/d/e/1FAIpQLScEbtj3DwYMg-lBV1pywvwt8WsYGfkg2DjrYKdXHYD6_6yWnw/viewform?embedded=true"
        width="100%"
        height="901"
        frameBorder="0"
        marginHeight="0"
        marginWidth="0"
      >
        Loading...
      </iframe>
    </Paper>
  </Layout>
);

export default withRoot(withStyles(styles)(ContactPage));
// export default TagsPage;
